import {Injectable} from '@angular/core';
import {AppState, selectGroupList} from '../../reducers/index-reducer';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs/Observable';
import {SubModule} from '../../pages/project/project';
import {PermissionService} from '../config/permission-service';



export const project = 'project';

export const mission = 'mission';

export const mine = 'mine';

const attendance: SubModule = {
  image: '',
  text: ''
};


@Injectable()
export class SubmoduleService {

  character: Observable<string>;

  constructor(public store: Store<AppState>,
              public permission: PermissionService) {

    this.character = store.select(selectGroupList).map(groups => groups[0]);
  }

  // createSubModules(moduleName: string): Observable<SubModule> {
    // return this.character
  // }
}



